// 获取公共域名地址
import serverUrl from '@/common/serverUrl.js';
export default {
	data() {
		return {
			globalUser: {}
		}
	},
	methods: {
		// 清理缓存
		cleanStorage() {
			uni.clearStorageSync();
			uni.showToast({
				title: "清理缓存成功",
				mask: false,
				duration: 1500
			});
		},
		// 修改性别
		modifySex() {
			uni.navigateTo({
				url: "../sex/sex"
			});
		},
		// 修改昵称
		modifyNicknam() {
			uni.navigateTo({
				url: "../meNickname/meNickname"
			});
		},
		// 修改生日
		modifyBirthday() {
			uni.navigateTo({
				url: "../meBirthday/meBirthday"
			});
		},
		// 修改头像
		operator() {
			const globalUser = this.getGlobalUser("globalUser");
			uni.showActionSheet({
				itemList: ["查看我的头像", "从相册选择上传"],
				success: res => {
					const index = res.tapIndex;
					if (index == 0) {
						// 预览头像
						let faceArr = [];
						faceArr.push(globalUser.faceImage);
						uni.previewImage({
							urls: faceArr,
							current:faceArr[0]
						})
					} else if (index == 1) {
						// 选择上传头像
						uni.chooseImage({
							count:1,
							sizeType:["compressed"],
							sourceType: ["album"],
							success: res => {
								// 获得临时路径
								const tempFilePath =  res.tempFilePaths[0];





								

								uni.navigateTo({
									url: "../faceCrop/faceCrop?tempFilePath=" + tempFilePath
								});

							}
						});
					}
				}
			})
		},
		logOut() {
			const qq = serverUrl.qq;
			const url = serverUrl.serverUrl;
			const globalUser = this.getGlobalUser("globalUser");
			uni.request({
				url: url + '/user/logout?qq=' + qq + '&userId=' + globalUser.id,
				method: 'POST',
				success: res => {
					if (res.data.status == 200) {
						uni.removeStorageSync("globalUser");
						uni.switchTab({
							url: '../me/me'
						});
					}
				},
				fail: () => {
					console.log('退出失败');
				}
			});
		}
	},
	onShow() {
		const globalUser =  this.getGlobalUser("globalUser");
		this.globalUser = globalUser;
	}
}
